body {
  margin: 0;
  padding: 0;
  font-family: -apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Oxygen-Sans,Ubuntu,Cantarell,Helvetica Neue,sans-serif;
  -moz-osx-font-smoothing: grayscale;
}

body {
  display: flex;
  justify-content: center; }

.App {
  max-width: 960px; }

.separator {
  border-bottom: 1px solid #eee;
  width: 100%;
  margin: 1rem 0; }

.dot-separator::after {
  content: "\2219";
  padding: 0 .5rem; }

.Header {
  height: 4rem;
  font-size: .875rem;
  display: flex;
  flex: 1 1;
  align-items: center;
  justify-content: flex-end; }
  .Header a {
    text-decoration: none; }
    .Header a:hover {
      box-shadow: 0 1.5px 0 0 #FF2353; }

.name {
  font-size: 3rem;
  font-weight: 700;
  text-align: center;
  padding: 0;
  margin: 0; }

.titles {
  align-items: center;
  display: flex;
  font-size: 1.065rem;
  justify-content: center;
  line-height: normal;
  padding: .5rem 0; }
  .titles .title-item {
    background-color: #c1e4ff;
    border-radius: 1px;
    color: #091b33;
    padding: .065rem .375rem .125rem; }
    .titles .title-item .highlight {
      background-color: #c1e4ff;
      border-radius: 1px;
      color: #091b33;
      padding: .065rem .375rem .125rem; }

.social-container {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: .125rem 0; }
  .social-container a {
    margin: .25rem .5rem;
    text-decoration: none;
    font-size: 2rem;
    color: #333;
    -webkit-transform: perspective(1px) translateZ(0);
            transform: perspective(1px) translateZ(0);
    box-shadow: 0 0 1px transparent; }
    .social-container a .svg-inline--fa {
      height: 1em; }

.link-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-bottom: 15px; }
  .link-container .link-item {
    align-items: baseline;
    display: flex;
    flex-wrap: wrap;
    margin: .75rem 0; }
    .link-container .link-item a {
      display: flex;
      text-decoration: none; }
      .link-container .link-item a:hover {
        color: #FF2353; }
        .link-container .link-item a:hover .description {
          color: #FF2353; }
      .link-container .link-item a .title {
        font-size: 1rem;
        font-weight: 400; }
      .link-container .link-item a .description {
        font-size: .95rem;
        color: #8e8e8e; }

